import { Button, TextField } from '@mui/material';
import React from 'react';
import './style.css';

function Signup() {

   const handleClick = () => {

   }

  return (
    <div class="loginpage">
    <div class="backgroundBox">
        <div class="division-1">
            <div class="sub-div-1">
                <div class="logbox">
                    <div class="googlesign">
                        <p class="signinWithGoogle">Create Your Account</p>
                    </div>
                    <div class="fullname">
                        <div class="firstnamebox">
                        <TextField fullWidth id="standard-basic" type='text' label="First Name" variant="standard"/>
                        </div>
                        <div class="lastnamebox">
                        <TextField fullWidth id="standard-basic" type='text' label="Last Name" variant="standard" />
                        </div>
                    </div>
                    <div class="namebox">
                    <TextField fullWidth type='email' id="standard-basic" label="Email address" variant="standard" />
                    </div>
                    <div class="passbox">
                        <TextField fullWidth id="standard-basic" type='password' label="Password" variant="standard" />
                    </div>
                    <div class="logBotton">
                        {/* <button onClick={handleClick}>Create Account</button> */}
                        <Button onClick={handleClick} size="small" variant='contained' >Create Account</Button>
                    </div>
                </div>
            </div>

        </div>
        <div class="division-2">
            <p>My Task</p>
            <div class="description">
                <span>Manage Your Task</span>
            </div>
        </div>

    </div>
</div>
  )
}

export default Signup


