import React from 'react';
import CloseIcon from '@mui/icons-material/Close';
import styles from './style.module.css';
import TextareaAutosize from '@mui/base/TextareaAutosize';
import {Button, Dialog, AppBar, Toolbar, IconButton, Typography, Slide, Box, Grid, TextField} from '@mui/material';
import axios from 'axios';
import { useUserAuth } from '../../../context/UserAuthContext';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
  });

function CreateTask({props}) {
    const {handleClose, open, setOpen, setReload} = props;
    let {user} = useUserAuth();
    
      const handleSubmit = async(event) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        // console.log({
        //   title: data.get('title'),
        //   description: data.get('description'),
        // });
       let mydata = {
          title: data.get('title'),
          description: data.get('description'),
        };
        // console.log(mydata);
        // console.log("token", user.token);
        axios.post(`https://todosmytask.herokuapp.com/${user.id}/tasks`, mydata, {
            headers: {
              'Authorization': `bearer ${user.token}`
            }}
            )
        .then((res) => {
            // console.log("res", res);
            setReload((prev) => {
               return prev + 1
            });
            setOpen(false);
        })
        .catch(err => console.log(err));
      };



  return (
    <>
      <Dialog
        fullScreen
        open={open}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        <AppBar color='success' sx={{ position: 'relative' }}>
          <Toolbar>
            <IconButton
              edge="start"
              color="inherit"
              onClick={handleClose}
              aria-label="close"
            >
              <CloseIcon />
            </IconButton>
            <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
              Create New Task
            </Typography>
          </Toolbar>
        </AppBar>
        <Box component='form' onSubmit={handleSubmit} sx={{mt:3}}>
          <Grid container spacing={2} 
          sx={{
            boxSizing:'border-box',
            padding: '1rem 2rem',
            display:'flex',
            flexDirection:'column',
            justifyContent:'center',
            alignItems:'center'
            }}>
              <Grid item sx={{
                display:'flex',
                width:'100%',
                maxWidth:'50rem'
              }}>
                <TextField
                autoComplete="given-title"
                required
                name="title"
                fullWidth
                id="title"
                label="Title"
                />
              </Grid>
              <Grid className={styles.grid} item sx={{
                display:'flex',
                width:'100%',
                maxWidth:'50rem'
              }}>
              <TextareaAutosize
              className={styles.textarea}
                aria-label="minimum height"
                minRows={3}
                placeholder="Description"
                color='primary'
                name='description'
                id='description'
                style={{ width: '100%',
                  borderRadius:3,
                  boxSizing: 'border-box',
                }}
              />
              </Grid>
              <Button 
              type='submit' 
              variant='outlined'
              size='large'
              color='success'
              >Create</Button>
          </Grid>
        </Box>
      </Dialog>
    </>
  )
}

export default CreateTask;
