import { Card, CardContent, Typography, IconButton } from '@mui/material';
import { Stack } from '@mui/system';
import DeleteIcon from '@mui/icons-material/Delete';
import React from 'react';

function CardRenderer({props}) {
    const {title, description, deleteCard, id} = props;
    
  return (
    <div>
       <Card>
        <CardContent>
          <Stack display='block' position='relative' direction='column'>
          <Typography component='div' variant='h6' gutterBottom>
                   {title}
            </Typography>
            <Typography variant='body2' color='text.secondary'>
                  {description}
            </Typography>
            <IconButton onClick={() => deleteCard(id)} sx={{position:'absolute', right:0, top:0}} aria-label="delete">
            <DeleteIcon/>
            </IconButton>
          </Stack>
        </CardContent>
       </Card>
    </div>
  )
}

export default CardRenderer;
