import React, { useEffect, useState } from 'react';
import {Fab, Box, TextField, Stack} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import styles from './style.module.css';
import Autocomplete from '@mui/material/Autocomplete';
import CardRenderer from '../CardRenderer/CardRenderer';
import CreateTask from './CreateTask/CreateTask';
import axios from 'axios';
import {useUserAuth} from '../../context/UserAuthContext';


function Dashboard() {
  let {user} = useUserAuth();
  const [cards, setCards] = useState([]);
  const [open, setOpen] = useState(false);
  const [reload, setReload] = useState(0);
  const [options, setOptions] = useState([]);
  const [emptyArray, setEmptyArray] = useState(false);
  const [runEmptyArrayCounter, setRunEmptyArrayCounter] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

const handleClose = () => {
    setOpen(false);
};

const handleChange = (event) => {
    setRunEmptyArrayCounter(true);
    setCards(options.filter((task) => task.title.toUpperCase().includes(event.target.value.toUpperCase())).map((task) => <CardRenderer key={task._id} props={{title:task.title, description:task.description, deleteCard:deleteCard, id:task._id}}/>));
}

const deleteCard = async(id) => {
  console.log(id);
     await axios.delete(`https://todosmytask.herokuapp.com/${user.id}/tasks/${id}`, {
      headers: {
        'Authorization': `bearer ${user.token}`
      }})
      .then((res) => {
        // console.log(res);
        setReload((prev) => prev -1);
      })
      .catch((err) => {
        console.log(err);
      });
}

useEffect(() => {
  if(runEmptyArrayCounter){
    if(cards.length === 0){
    setEmptyArray(true);
    }else{
      if(emptyArray){
        setEmptyArray(false);
      }
    }
  }
}, [cards]);

  useEffect(() => {
    axios.get(`https://todosmytask.herokuapp.com/${user.id}/tasks`, {
      headers: {
        'Authorization': `bearer ${user.token}`
      }}
      )
  .then((res) => {
      // console.log("res", res.data);
      setOptions(res.data);
  })
  .catch(err => console.log(err));
  }, [reload, user.id, user.token]);


  useEffect(() => {
    setCards(options.map((task) => <CardRenderer key={task._id} props={{title:task.title, description:task.description, deleteCard:deleteCard, id:task._id}}/>));
  }, [options]);

  return (
    <div className={styles.dashboard}>
   <Fab color="primary" aria-label="add" className={styles.addIcon} onClick={handleClickOpen}>
     <AddIcon />
   </Fab>
   <Box mt={2} width='100%' maxWidth='50rem'>
   <Autocomplete
        freeSolo
        id="free-solo-2-demo"
        autoComplete
        disableClearable
        options={options.map((option) => option.title)}
        renderInput={(params) => (
          <TextField
          onChange={handleChange}
            {...params}
            label="Search input"
            InputProps={{
              ...params.InputProps,
              type: 'search',
            }}
          />
        )}
      />
      <Stack direction='column' spacing={4} mt={4} mb={4}>
      {emptyArray ? (<p>Task does not exist.</p>):(cards)}
      </Stack>
   </Box>
   <CreateTask props = {{handleClose, open, setOpen, setReload}}/>
    </div>
  )
}

export default Dashboard;