import { createContext, useContext, useEffect, useState } from "react";
import {gapi} from "gapi-script";
import axios, { Axios } from 'axios';
import { useNavigate } from 'react-router-dom';
import { LinearProgress } from "@mui/material";
import AnimationVerifyUser from "../components/Animation/AnimationVerifyUser";

const UserAuthContext = createContext();

export const UserAuthProvider = ({children}) => {

    const navigate = useNavigate();
    const [loading, setLoading] = useState(true);
    var [GoogleAuth, setGoogleAuth] = useState(null);
    let user = localStorage.getItem("user") || null;
    // console.log("localStorage.getItem('user')--",localStorage.getItem("user"));

    if(user){
      user = JSON.parse(localStorage.getItem("user"));
      // console.log("JSON.parse(localStorage.getItem('user'))===",JSON.parse(localStorage.getItem("user")))
    };

    // console.log("user: get ",user);

    const GoogleSignIn = () => {
        // GoogleAuth.signIn().then(async(res) => {
        //   localStorage.clear();
        //     console.log("res: ",res);
        //   })
        //   .catch((err) => console.log("error from auth====",err));
        axios.get('/index/googleAuth').then(res => {
          console.log(res);
        })
        .catch((err) => {console.log(err)});
    };

    useEffect(() => {
      if(user){
        axios.get('https://todosmytask.herokuapp.com/users/checkJWTtoken', {
          headers: {
            'Authorization': `bearer ${user.token}`
          }})
      .then((res) => {
      // console.log("res user==", res.data);
      if(res.data.success === true){
        setTimeout(() => {
          setLoading(false);
        }, 1000);
      }else{
        localStorage.clear();
        setTimeout(() => {
          setLoading(false);
          navigate('/login');
        }, 1000);
      }
      })
      .catch(err => {
        console.log("from axios--",err);
        localStorage.clear();
        setTimeout(() => {
          setLoading(false);
          navigate('/login');
        }, 1000);
      }); 
      }
      else{
        setTimeout(() => {
          setLoading(false);
        }, 1000);
      }
    }, []);

    // useEffect(() => {console.log(loading)}, [loading]);

    useEffect(()=> {
        const initClient = () => {
            gapi.client.init({
              'apiKey': 'AIzaSyAXNYX7JKl4ZupNKNwM55VB5DOP8ZgD1wo',
              'clientId': '842134020386-85iacjs1smm9e923hurunt1l4gi3gp5m.apps.googleusercontent.com',
              'scope': 'https://www.googleapis.com/auth/calendar'
          }).then(() => {
            setGoogleAuth(gapi.auth2.getAuthInstance());
              var currentUser = gapi.auth2.getAuthInstance().currentUser.get();
                console.log("current user: ",currentUser);
            // GoogleAuth.isSignedIn.listen(updateSigninStatus);
            // setSigninStatus();
          });
          }

        gapi.load('client:auth2', initClient);
    }, []);

   return (false ? (
    <div style={{width: '100%',
      position: 'absolute',
      display: 'flex',
      height: '100%',
      justifyContent: 'center',
      alignContent: 'center'
      }}>
     <LinearProgress sx={{position: 'fixed', top:0, left: 0, right: 0}}/>
     <AnimationVerifyUser/>
    </div>
   ) : (
    <UserAuthContext.Provider value={{GoogleSignIn, user}}>
    {children}
    </UserAuthContext.Provider>
   )
   );
};

export const useUserAuth = () => {
      return useContext(UserAuthContext);
}