import { Stack, Avatar, Typography, Button } from '@mui/material';
import {blue } from '@mui/material/colors';
import React from 'react';
import styles from './Style.module.css';
import AssignmentIcon from '@mui/icons-material/Assignment';
import { NavLink } from 'react-router-dom';
import { useUserAuth } from '../../context/UserAuthContext';

function Navbar() {

  const {user} = useUserAuth();
  // console.log("Navbar----");

  return (
  <div className={styles.Navbar}>
    <Stack direction='row' spacing={1.2}>
     <Avatar sx={{ bgcolor: blue[700], cursor: 'pointer' }}>
      <AssignmentIcon />
     </Avatar>
     <Stack className={styles.Typography} sx = {{display:'flex', justifyContent:'center'}}>
     <Typography className={styles.font} variant='h5' sx={{fontWeight: 600}}>
         My Task
     </Typography>
     </Stack>
    </Stack>
    <div style={{flex:1}}/>
    <Stack direction='row' spacing={2}>
    {user? (
    <Avatar 
    sx={{ bgcolor: blue[600], cursor:'pointer'}} 
    src={user.imgLink}>{user.firstname.substr(0, 1)}
    </Avatar>
    ) : (
    <NavLink style={{textDecoration:'none'}} to='/login'>
      <Button 
      variant='contained' 
      color='info' disableRipple disableElevation>Login</Button>
    </NavLink>
    )}
    </Stack>
  </div>
    )
}

export default Navbar;
